export const bakalavr = {
  uz: {
    about:
      "Oliy ta’lim muassasalarining bakalavriatiga talabalarni o‘qishga qabul qilish tartibi 393-sonli qaroriga asosan amalga oshiriladi.",
    aboutData: [
      `Unga ko’ra, Oliy ta’lim muassasalari qabulni O‘zbekiston Respublikasi Prezidentining yoki Vazirlar Mahkamasining tegishli qarori bilan tasdiqlangan davlat grantlari va to‘lov-kontrakt asosida qabul kvotalariga muvofiq amalga oshiradilar.`,
      ``,
      `Oliy ta’lim muassasalarining kunduzgi, sirtqi, maxsus sirtqi va kechki (smenali) ta’lim shakllariga talabalarni qabul qilish O‘zbekiston Respublikasi ta’lim muassasalariga o‘qishga qabul qilish bo‘yicha Davlat komissiyasi (keyingi o‘rinlarda Davlat komissiyasi deb ataladi) tomonidan kirish imtihonlari (test sinovlari, kasbiy (ijodiy) imtihon, yozma imtihon) natijalariga ko‘ra to‘plangan ballarning reyting tizimi bo‘yicha amalga oshiriladi.`,
      ``,
      `O‘zbekiston Respublikasi Prezidentining yoki Vazirlar Mahkamasining tegishli qarori bilan tasdiqlangan to‘lov-kontrakt asosida qabul kvotalari doirasida hamda qabul kvotalaridan tashqari abituriyentlarni o‘qishga qabul qilish Davlat komissiyasi tomonidan mazkur Nizomda belgilangan tartibda amalga oshiriladi.`,
      `Oliy ta’lim muassasalariga qabul barcha uchun (ham grantlar, ham to‘lov-kontrakt bo‘yicha) teng huquqlilik, yagona qabul qoidalari va yagona tanlov asosida amalga oshirilib, test sinovlarida eng yuqori ball to‘plagan abituriyentlarning davlat grantlari bo‘yicha birinchi navbatda qabul qilinish huquqi ta’minlanadi. Qolgan abituriyentlar test ballari reytingi asosida belgilangan to‘lov-kontrakt kvotalari doirasida qabul qilinish huquqiga egadirlar.`,
      `Tanlov har bir oliy ta’lim muassasasi bo‘yicha ta’lim yo‘nalishlari, o‘qitish tillari va shakllariga ko‘ra alohida o‘tkaziladi. Ayrim yo‘nalishlar bo‘yicha hududlar kesimida maqsadli qabul kvotalari tasdiqlangan taqdirda, tanlov har bir hudud bo‘yicha alohida o‘tkaziladi. `,
      `Test sinovlari O‘zbekiston Respublikasi Prezidentining 2019-yil 14-maydagi PQ-4319-son qaroriga muvofiq iyul-avgust oylarida o‘tkaziladi.Abituriyentlarni ro’yxatga olish 2022-yil my.dtm.uz platformasi yoki my.gov.uz sayti orqali onlayn amalga oshirildi. Ro‘yxatga olish 2022-yil 20-iyundan 20-iyulga qadar amalga oshirilgan.`,
      ``,
      `Abituriyentlarga oliy ta’lim muassasalarining beshtagacha bakalavriat ta’lim yo‘nalishida tanlovda ishtirok etish huquqi berilgan.`,
      `O‘zbekiston Respublikasi Prezidentining 2019-yil 14-maydagi PQ-4319-son qarori va 2022-yil 20-apreldagi PF-113-son Farmoniga muvofiq quyidagi abituriyentlar test sinovi uchun to‘lovdan ozod etilgan:`,
      `− test sinovlari tashkil etilayotgan yilda ta’lim muassasalarini tugatgan abituriyentlar;`,
      `− nogironligi bo‘lgan shaxslar;`,
      `− to‘liq davlat ta’minotidagi shaxslar (“Mehribonlik” uyi va Bolalar shaharchasining bitiruvchilari);`,
      `− “Ijtimoiy himoya yagona reestri” axborot tizimida ro‘yxatga olingan shaxslar va ularning farzandlari;`,
      `− “Yoshlar daftari”ga kiritilgan yoshlar;`,
      `− imtiyoz asosida to‘g‘ridan to‘g‘ri talabalikka tavsiya etiladigan abituriyentlar.                                                                              `,
      `Qolgan abituriyentlar uchun test sinovi pullik sanaladi(2022-yil uchun 150000 so’m).`,
      `Davlat komissiyasining 13-son bayoni bilan 2022-2023-o‘quv yili uchun respublika oliy ta’lim muassasalarining bakalavriat ta’lim yo‘nalishlariga mos test sinovi topshiriladigan fanlar majmuasi tasdiqlangan. Ushbu fanlar majmuasi bilan quyidagi havola orqali tanishish mumkin:`,
      {link:"https://t.me/dtmuzb/4673", text:"@dtmuzb/4673"},
      ``,
      `Barcha test topshiruvchilar uchun majburiy bo‘lgan 3 ta fan bo‘yicha:
         Ona tili (o‘zbek, rus yoki qoraqalpoq tili) - 10 ta savol.`,
      ` Har biri uchun 1,1 balldan.`,
      `Maksimal ball – 11 ball.`,
      ``,
      `Matematika - 10 ta savol.`,
      `Har biri uchun 1,1 balldan.`,
      `Maksimal ball – 11 ball.`,
      ``,
      `O‘zbekiston tarixi - 10 ta savol.`,
      `Har biri uchun 1,1 balldan.`,
      `Maksimal ball – 11 ball.`,
      ``,
      `Jami to‘plash mumkin bo‘lgan ball – 33 ball beriladi.`,
      ``,
      `Bakalavriat ta’lim yo‘nalishiga mos bo‘lgan 2 ta fan bo‘yicha:`,
      `1-fan - 30 ta savol.`,
      `Har biri uchun 3,1 balldan.`,
      `Maksimal ball – 93 ball.`,
      `2-fan - 30 ta savol.`,
      `Har biri uchun 2,1 balldan.`,
      `Maksimal ball – 63 ball.`,
      `Jami to‘plash mumkin bo‘lgan ball – 156 ball beriladi.`,
      ``,
      `Umumiy holda, 2022-2023 o‘quv yili qabulida abituriyentlarga 5 ta fan bo‘yicha jami 90 ta test topshirig‘i beriladi. Bunda to‘plash mumkin bo‘lgan maksimal ball – 189 ballni tashkil etadi.
        Vazirlar Mahkamasining 2018-yil 3-apreldagi 261-son qarori bilan tasdiqlangan nizomda test topshiriqlarini bajarish uchun (javoblar varaqasini to‘ldirish bilan birga) har 30 ta test topshirig‘iga 1 soat vaqt ajratilishi belgilangan.`,
      `Shundan kelib chiqqan holda 2022-2023-o‘quv yili uchun oliy ta’lim muassasalarining bakalavriat ta’lim yo‘nalishlariga kirish test sinovlarida 90 ta test topshirig‘i uchun 3 soat vaqt beriladi.`,
      `Agar abituriyentda mutaxassislik fanining bittasidan sertifikat mavjud bo‘lsa yoki bitta fandan kasbiy (ijodiy) imtihon topshirsa, unga 1 ta mutaxassislik va 3 ta majburiy fan uchun 2 soat vaqt ajratiladi.`,
      `Agar abituriyent faqat majburiy fanlar bo‘yicha test sinovida ishtirok etsa, unga 3 ta majburiy fan bo‘yicha 30 ta test topshirig‘i uchun 1 soat vaqt beriladi.`,
      `Test sinovining bunday shakli sirtqi, kechki va xorijdan ko’chirayotganlarga ham bir xil shaklda bo’ladi.`,
    ],
    questions:[{
      title:"Men harbiy qism tavsiyanomasiga egaman. Men ushbu taysiyanomam bilan 5 ta yo‘nalishni tanlay olamanmi?  Agar mumkin bo‘lsa imtiyozim qanday?",
      content:`Harbiy qism qo‘mondonligining tavsiyanomasiga ega bo‘lgan abiturientlar ham 5 tagacha bakalavriat ta'lim yo‘nalishini tanlashi mumkin. Faqatgina ushbu tavsiyanoma bo‘yicha imtiyoz birinchi tanlangan ta'lim yo‘nalishiga nisbatan tatbiq etiladi. Agar talaba test sinovi natijalariga ko‘ra imtiyozi bo‘yicha birinchi ta'lim yo‘nalishiga talabalikka tavsiya etilmaganda, to‘plagan bali bilan tanlangan barcha ta'lim yo‘nalishlariga umumiy tanlovda ishtirok etadi`
    },
    {
      title:`Jiyanim Mahalla va oilani qo‘Ilab-quvvatlash vazirligining xotin-qizlar uchun berilgan taysiyanomasini oldi. Jiyanim ushbu taysiyanoma bilan oliy ta'lim muassasasiga davlat granti asosida o‘qishga qabul qilinadimi?`,
      content:`2021/2022 o‘quv yiliga xotin-qizlar uchun jami 2000 ta alohida davlat granti asosidagi qabul ko‘rsatkichlari hududlar kesimida ajratildi. Ushbu qabul ko‘rsatkichlariga tegishli tavsiyanomaga ega bo‘lgan xotin-qizlar orasidan eng yuqori ball to‘plaganlari qabul qilinadi. Tavsiyanomaga ega bo‘lgan ammo ajratilgan alohida davlat grantiga tavsiya etilmagan xotin-qizlar test sinovlari natijalariga ko‘ra to‘plagan ballari bilan tanlagan beshtagacha bakalavriat ta'lim yo‘nalishlariga umumiy tanlovda ishtirok etishi mumkin.`
    },
    {
      title:`Men Rossiya fuqarosiman O‘zbekistondagi OTMdan biriga o‘qishga kirmoqchiman. Buni qanday tartibda amalga oshiraman?`,
      content:`O‘zbekiston Respublikasidagi oliy ta'lim muassasalariga xorijiy fuqarolarni o‘qishga qabul qilish oliy ta'lim muassasasi tomonidan o‘tkaziladigan suhbat natijalariga ko‘ra amalga oshiriladi. Bunda, xorijiy fuqarolar o‘zlarini qiziqtirgan oliy ta'lim muassasasining sayti orqali abituriyent sifatida ro‘yxatdan o‘tadi.`
    },
    {
      title:`Oliy ta'lim muassasalarida to‘lov-kontrakt bo‘yicha ta'lim olgan bitiruvchilar majburiy ishga joylashtirilishi mumkinmi?`,
      content:`Oliy ta'lim muassasalarida o‘z hisobidan to‘lov-kontrakt bo‘yicha ta'lim olgan bitiruvchilar o‘z ixtiyori bilan taqsimot komissiyasida ishtirok etishi va uning roziligi asosida komissiya qarori bilan taklif etilayotgan ish joylariga ishga yuborilishi mumkin. Bunda, bitiruvchilarning taqsimoti oliy ta'lim muassasasi rektori tomonidan tasdiqlangan oliy ta'lim muassasasining taqsimot komissiyasi tomonidan o‘tkaziladi.`
    },
    {
      title:`Harbiy qism taysiyanomasini oldim. Shu taysiyanomam sirtqi ta'lim shakliga ham o‘tadimi?`,
      content:`2021/2022 o‘quv yiliga harbiy qism qo‘mondonligining tavsiyanomasiga ega bo‘lganlar uchun oliy ta'lim muassasalari hamda ta'lim shakllari (kunduzgi, sirtqi, kechki) kesimida alohida qabul ko‘rsatkichlari tasdiqlandi. Yuqoridagilardan kelib chiqib, harbiy qism tavsiyanomasiga ega bo‘lgan abiturient sirtqi ta'lim shakli uchun ajratilgan qabul ko‘rsatkichiga da'vogar bo‘lishi mumkin`
    },
    {
      title:`Men fuqaroligi yo‘q shaxs sifatida bakalavriatga o‘qishga kirish uchun onlayn tartibida ro‘yxatdan o‘ta olmayapman. Men kabilarga boshqacha tartib bormi?`,
      content:`Fuqaroligi yo‘q shaxslarni bakalavriatga kirish uchun ro‘yxatdan o‘tkazish oliy ta'lim muassasalari qabul komissiyalari tomonidan amalga oshiriladi. Shunga ko‘ra, Sizdan o‘zingizni qiziqtirgan oliy ta'lim muassasasi qabul komissiyasiga murojaat qilishingiz so‘raladi. `
    },
    {
      title:`Men bankda 11 yildan beri ishlayman. Ma'Iumotim o‘rta-maxsus. 5 yil ish stajiga ega bo‘lganlarni suhbat asosida imtihonlarsiz qabul qilish aytilgan edi. Men shu imtiyoz orqali o‘qishga kira olamanmi?`,
      content:`Respublika OTMning sirtqi va kechki ta'lim shakliga suhbat natijalariga ko‘ra tabaqalashtirilgan to‘lov-kontrakt asosida o‘qishga tavsiya etish huquqiga ega bo‘lgan vazirlik, idora va yirik xo‘jalik birlashmalarining ro‘yxati har yili Davlat komissiyasi tomonidan tasdiqlanadi. 2021/2022 o‘quv yili uchun tavsiyanoma berish huquqiga ega bo‘lgan vazirlik va idoralar ro‘yxati tasdiqlangandan so‘ng, Siz faoliyat ko‘rsatayotgan bankning respublika bosh ofisi tomonidan berilgan tavsiyanomaga asosan suhbat asosida imtihonlarsiz, tabaqalashtirilgan kontrakt bo‘yicha o‘qishga kirishingiz mumkin. Ma'lumot uchun: 2020/2021 o‘quv yili uchun tavsiyanoma berish huquqiga ega bo‘lgan vazirlik va idoralar ro‘yxatida O‘zbekiston Respublikasi Markaziy banki, tijorat banklari nazarda tutilgan edi.`
    },
    {
      title:`Vazirlik, idora va yirik xo‘jalik birlashmalarining ehtiyojlaridan kelib chiqib, ularning murojaatlariga asosan o‘qishga qabul qilingan talabalarning tabaqalashtirilgan to‘lov-kontrakt miqdorini bazaviy to‘lov-kontrakt miqdorida to‘lashga ruxsat beriladimi?`,
      content:`O‘zbekiston Respublikasi Prezidentining 2019-yil 17 iyundagi “2019/2020 o‘quv yilida O‘zbekiston Respublikasining oliy ta'lim muassasalariga o‘qishga qabul qilishning davlat buyurtmasi parametrlari to‘g‘risida”gi PQ-4359-son (<a href="https://lex.uz/docs/-4380357">lex.uz/docs/-4380357</a>) qarorining 6-bandiga muvofiq, oliy ta'lim muassasalariga 2019/2020 o‘quv yilidan boshlab vazirlik, idora va yirik xo‘jalik birlashmalarining (ish beruvchilar) ehtiyojlaridan kelib chiqib, kunduzgi bakalavriat ta'lim yo‘nalishlariga mos bo‘lgan yo‘nalishlar doirasida tabaqalashtirilgan to‘lov-kontrakt asosida sirtqi va kechki ta'lim shaklida o‘qitishni tashkil etish huquqi berilgan. O‘zbekiston Respublikasi ta'lim muassasalariga o‘qishga qabul qilish bo‘yicha Davlat komissiyasining tegishli bayoniga muvofiq respublika oliy ta'lim muassasalariga iqtisodiyot tarmoqlarida kamida 5 yil stajiga ega bo‘lgan va tegishli tavsiyanoma asosida suhbat bilan qabul qilinganlar uchun to‘lov-kontrakt miqdorlari 2019/2020 o‘quv yilidan boshlab mos ravishda har yili amalda belgilanadigan bakalavriat ta'lim yo‘nalishlari va shakli bo‘yicha bazaviy to‘lov-kontrakt qiymati miqdorlariga nisbatan 2 baravari miqdorida belgilangan.`
    },
    {
      title:`Xorijiy fuqarolami oliy ta'lim muassasalariga qabul qilish qanday tartib asosida amalga oshiriladi?`,
      content:`Xorijiy fuqarolarni O’zbekiston oliy ta’lim muassasalariga qabul qilish Vazirlar Mahkamasining “Xorijiy fuqarolarni O’zbekiston Respublikasi ta’lim muassasalariga qabul qilish va o’qitish tartibini takomillashtirish to’g’risida”gi 2008 yil 4 avgustdagi 169-sonli ( <a href="https://lex.uz/docs/-1380636">lex.uz/docs/-1380636</a> ) qaroriga muvofiq amalga oshiriladi. Xorijiy fuqarolarning hujjatlari O’zbekiston Respublikasi fuqarolari uchun belgilangan muddatlarda ya’ni bakalavriatga kirish uchun 20 iyundan 20 iyulga qadar, magistraturaga kirish uchun 1 iyuldan 30 iyulga qadar qabul qilinadi. O’rnatilgan tartibga binoan xorijiy fuqarolar hujjatlarini tegishli oliy ta’lim muassasasining Qabul komissiyasiga to’g’ridan-to’g’ri topshirishlari kerak. Xorijiy fuqarolar bakalavriatga Vazirlar Mahkamasi huzuridagi Davlat test markazi tomonidan har yili 1 avgust kuni o’tkaziladigan test sinovlari natijalari asosida qabul qilinadilar. Magistraturaga esa oliy ta’lim muassasalari tomonidan mustaqil ravishda tahminan 1-10 avgust oralig’ida tashkil etiladigan kirish imtihonlari natijasi asosida qabul qilinadilar. Xorijiy fuqarolarni talabalar safiga qabul qilish Vazirlar Mahkamasi huzuridagi O’zbekiston Respublikasi Ta’lim muassasalariga qabul qilish bo’yicha Davlat komissiyasining qarori asosida test sinovlari (bakalavriat uchun) yoki kirish imtihonlari (magistratura uchun) natijalariga ko’ra amalga oshiriladi.`
    },
    {
      title:`Xorijiy fuqarolaming o‘qish uchun to‘lovlari qanday tartibda amalga oshiriladi? `,
      content:`Xorijiy fuqarolar o’qishga faqat to’lov-shartnoma asosida qabul qilinib, ular to’lovlarini xorijiy fuqarolar uchun belgilangan miqdorida amalga oshiradilar. Xorijiy fuqarolar o’qish uchun to’lovlarini chet el valyutasi ekvivalentida, Markaziy bank tomonidan to’lov kunida belgilangan kurs asosida milliy valyutada amalga oshiradilar. Shu bilan birga xorijiy fuqarolar to’lov amalga oshirilganligi to’g’risidagi bank to’lov topshiriqnomasi yoki kvitantsiyasi bilan birga O’zbekiston Respublikasi banklarida chet el valyutasini milliy valyutaga ayirboshlanganligi to’g’risida sertifikatni ham topshiradilar.
      O’zbekiston Respublikasida yashash guvohnomasiga ega bo’lgan xorijiy fuqarolar bundan mustasno. Ular to’lovlarni chet el valyutasi ekvivalentida, Markaziy bank tomonidan to’lov kunida belgilangan kurs asosida, milliy valyutada amalga oshiriladilar. Ulardan chet el valyutasini milliy valyutaga ayirboshlanganligi to’g’risidagi sertifikat talab etilmaydi.`
    },
    {
      title:`Xorijiy fuqarolaming o‘qish uchun to‘lovlari qanday tartibda amalga oshiriladi? `,
      content:`“Kadrlarni davlat grantlari asosida maqsadli tayyorlash tartibi to’g’risida Nizom”ga muvofiq, agar bitiruvchi oliy ta’lim muassasalarining bakalavriati va magistraturasida davlat grantlari asosida o’qigan taqdirda, uning ishlab berish majburiyati kamida uch yil deb hisoblanadi.`
    }
  ],
  question:`Tez-tez so'raladigan savollar....`
  },

  ru: {
    about: `Порядок приема обучающихся в бакалавриат высших учебных заведений осуществляется на основании решения № 393.`,
    aboutData: [
      `Согласно ему высшие учебные заведения осуществляют прием в соответствии с квотами приема на основании государственных грантов и платежных договоров, утверждаемых соответствующим постановлением Президента Республики Узбекистан или Кабинета Министров.`,
      `Прием обучающихся на очную, заочную, специальную заочную и вечернюю (сменную) формы обучения высших учебных заведений осуществляется Государственной комиссией по приему в образовательные учреждения Республики Узбекистан (далее - Государственная комиссия) осуществляется по рейтинговой системе баллов, набранных по результатам вступительных испытаний (тестов, профессионального (творческого) экзамена, письменного экзамена).`,
      ``,
      `На основании договора об оплате труда, утверждаемого соответствующим постановлением Президента Республики Узбекистан или Кабинета Министров, прием абитуриентов в пределах квот приема и вне квот приема осуществляется Государственной комиссией в установленном порядке. в настоящем Регламенте.`,
      `Прием в высшие учебные заведения осуществляется на основе равных прав для всех (как по грантам, так и по контрактам), единых правил приема и единого отбора, обеспечивается право приема в очередь. Остальные абитуриенты имеют право быть принятыми в пределах расчетно-договорных квот, установленных на основании рейтинга тестовых баллов.`,
      `Конкурс проводится отдельно для каждого вуза по образовательным направлениям, языкам и формам обучения. В случае утверждения целевых квот приема в разрезе регионов по отдельным направлениям конкурс будет проводиться отдельно по каждому региону`,
      `Испытания пройдут в июле-августе в соответствии с постановлением Президента Республики Узбекистан №PQ-4319 от 14 мая 2019 года. Регистрация 2022 абитуриентов осуществлялась онлайн через платформу my.dtm.uz или сайт my.gov.uz. Регистрация проходила с 20 июня по 20 июля 2022 года.`,
      ``,
      `Абитуриенты имеют право участвовать в конкурсе до пяти бакалавров высших учебных заведений.`,
      `В соответствии с Указом Президента Республики Узбекистан №PQ-4319 от 14 мая 2019 года и Указом №ПФ-113 от 20 апреля 2022 года от платы за тестирование освобождаются:`,
      `− абитуриенты, окончившие учебные заведения в том году, когда проводятся испытания;`,
      `− лица с ограниченными возможностями;`,
      `− лица, находящиеся на полном государственном обеспечении (выпускники Дома "Мехрибонлик" и Детского городка);`,
      `− лица и их дети, зарегистрированные в информационной системе «Единого реестра социальной защиты»;`,
      `− Молодежь включена в «Реестр молодежи»;`,
      `− абитуриентов, рекомендованных непосредственно на обучение на основании предпочтений.`,
      ``,
      `Для остальных абитуриентов тест считается платным (150 000 сумов на 2022 год).`,
      `Согласно отчету № 13 Госкомиссии утвержден набор предметов на 2022-2023 учебный год для курсов бакалавриата высших учебных заведений республики. Ознакомиться с данным комплексом предметов можно по следующей ссылке:`,
      {link:"https://t.me/dtmuzb/4673", text:"@dtmuzb/4673"},
      `По 3 предметам, обязательным для всех испытуемых:
      Родной язык (узбекский, русский или каракалпакский язык) – 10 вопросов.`,
      `По 1,1 балла за каждое.`,
      `Максимальная оценка – 11 баллов.`,
      ``,
      `Математика - 10 вопросов.`,
      `По 1,1 балла за каждое.`,
      `Максимальная оценка – 11 баллов.`,
      ``,
      `История Узбекистана - 10 вопросов.`,
      `По 1,1 балла за каждое.`,
      `Максимальная оценка – 11 баллов.`,
      ``,
      `Суммарно возможная оценка – 33 балла.`,
      ``,
      `2 предмета, подходящие для бакалавриата:`,
      `Тема 1 - 30 вопросов.`,
      `3,1 балла за каждое.`,
      `Максимальная оценка – 93 балла.`,
      `Тема 2 - 30 вопросов.`,
      `По 2,1 балла за каждое.`,
      `Максимальная оценка – 63 балла.`,
      `Общее возможное количество баллов – 156 баллов.`,
      ``,
      ` В целом абитуриентам на 2022-2023 учебный год будет дано всего 90 тестовых заданий по 5 предметам. Максимально возможная оценка – 189 баллов.`,
      `Исходя из этого, на 2022-2023 учебный год на выполнение 90 тестовых заданий во вступительных испытаниях на образовательные курсы бакалавриата высших учебных заведений будет отводиться 3 часа.`,
      `Если абитуриент имеет сертификат по одному из предметов специальности или сдает профессиональный (творческий) экзамен по одному предмету, ему будет отведено 2 часа на 1 специальность и 3 обязательных предмета.`,
      `Положением, утвержденным постановлением Кабинета Министров № 261 от 3 апреля 2018 года, предусмотрено, что на выполнение тестовых заданий (включая заполнение листа ответов) на каждые 30 тестовых заданий будет отводиться 1 час.`,
      `Эта форма тестирования одинакова для заочников, вечерников и иностранных студентов.`,
    ],
    questions:[{
      title:"У меня есть военная привилегия. Могу ли я выбрать 5 направлений с этой привилегией ? Каковы мои предпочтения, если возможно ?",
      content:`По рекомендательному письму из воинской части также можно выбрать до 5 направлений бакалавриата. Только эта рекомендация относится к первой выбранной области обучения. Если по результатам тестирования обучающийся не рекомендован на первое образовательное направление, он с полученным баллом участвует в общем отборе по всем выбранным образовательным направлениям.`
    },
    {
      title:`Мой племянник получил справку, выданную Министерством поддержки семьи для женщин. Примут ли мою племянницу в вуз по государственному гранту?`,
      content:`В 2021-2022 учебном году по регионам выделено 2000 отдельных государственных грантовых приемных ставок для женщин. Среди женщин, имеющих рекомендательное письмо в соответствии с этими критериями приема, будет принята женщина с наивысшим баллом. Женщины, имеющие рекомендацию, но не рекомендованные на получение отдельного государственного гранта, могут участвовать в общем конкурсе до пяти курсов бакалавриата по своему выбору по результатам тестирования.`
    },
    {
      title:`Я гражданин России и хочу учиться в одном из высших учебных заведений Узбекистана. Как мне это сделать?`,
      content:`Прием иностранных граждан в высшие учебные заведения Республики Узбекистан осуществляется по результатам собеседования, проводимого высшим учебным заведением. В этом случае иностранные граждане регистрируются в качестве абитуриента через сайт интересующего их вуза.`
    },
    {
      title:`Выпускники, прошедшие обучение в высших учебных заведениях на платно-контрактной основе, подлежат обязательному трудоустройству?`,
      content:`Выпускники высших учебных заведений, получившие образование за счет собственных средств на платно-контрактной основе, могут добровольно участвовать в распределительной комиссии и на основании ее утверждения направляться на работу на предлагаемые по решению комиссии должности. В этом случае распределение выпускников осуществляется распределительной комиссией высшего учебного заведения, утверждаемой ректором высшего учебного заведения.`
    },
    {
      title:`Я получил военную привилегию. Эта привилегия распространяется и на заочное обучение?`,
      content:`На 2021/2022 учебный год имеют рекомендательное письмо от командования воинской части по разделу высших учебных заведений и формам обучения (дневная, заочная, вечерняя). На основании изложенного поступающий, имеющий рекомендательное письмо от воинской части, может быть кандидатом на приемную норму, отведенную для заочной формы обучения.`
    },
    {
      title:`Как негражданин, я не могу зарегистрироваться онлайн для обучения в бакалавриате. Есть ли другая процедура для таких, как я?`,
      content:`Регистрация лиц без гражданства для поступления на программы бакалавриата осуществляется приемными комиссиями высших учебных заведений. Соответственно, вас просят обратиться в приемную комиссию интересующего вас вуза.`
    },
    {
      title:`Работаю в банке 11 лет. Образование у меня средне-специальное. Было сказано, что тех, кто имеет стаж работы от 5 лет, будут принимать на основе собеседования без экзаменов. Могу ли я поступить на эту стипендию?`,
      content:`Перечень министерств, ведомств и крупных экономических объединений, которые имеют право рекомендовать обучение в заочной и вечерней формах высшего образования Республики Узбекистан на основе дифференцированной платы-контракта по результатам собеседования , ежегодно утверждается Государственной комиссией. После утверждения перечня министерств и ведомств, имеющих право выдачи рекомендации на 2021/2022 учебный год, на основании рекомендации, выданной республиканским головным офисом банка, в котором вы осуществляете свою деятельность, вы получите диплом о высшем образовании по дифференцированному контракту без экзаменов и на основании собеседования можно поступить в заведение. Для справки: Центральный банк Республики Узбекистан и коммерческие банки включены в перечень министерств и ведомств, имеющих право давать рекомендации на 2020/2021 учебный год.`
    },
    {
      title:`Разрешается ли, исходя из потребностей министерств, ведомств и крупных хозяйственных объединений, выплачивать дифференцированную гонорарно-контрактную сумму обучающимся, принятым на обучение на основании их заявлений, в размере базовой гонорарно-контрактной суммы?`,
      content:`ПП-4359 Президента Республики Узбекистан от 17 июня 2019 года «О параметрах государственного заказа на прием в высшие учебные заведения Республики Узбекистан в 2019/2020 учебном году» (<a href="https://lex.uz/docs/-4380357">lex.uz/docs/-4380357</a>), согласно п. 6 решения, начиная с 2019/2020 учебного года, исходя из потребностей министерств, ведомств и крупных бизнес-объединений (работодателей), высшим образовательные учреждения. Право на организацию обучения по заочной и вечерней формам обучения предоставляется на основе дифференцированного платного договора в пределах сферы образования. Согласно соответствующему заключению Государственной комиссии по приему в высшие учебные заведения Республики Узбекистан, лица, имеющие не менее 5 лет опыта работы в отраслях экономики и принятые в высшие учебные заведения Республики Узбекистан, имеют право на получение собеседование на основании рекомендательного письма, суммы контрактов определяются в размере 2-кратной суммы годового контракта, начиная с 2019/2020 учебного года.`
    },
    {
      title:`Каков порядок приема иностранного гражданина в высшие учебные заведения?`,
      content:`№ 169 Кабинета Министров August 4, 2008«О совершенствовании порядка приема и обучения иностранных граждан в образовательные учреждения Республики Узбекистан» Прием иностранных граждан в высшие учебные заведения Республики Узбекистан (<a href="https://lex.uz/docs/-1380636">lex.uz/docs/-1380636</a> ) осуществляется в соответствии с решением. Документы иностранных граждан принимаются в сроки, установленные для граждан Республики Узбекистан, т.е. с 20 июня по 20 июля для бакалавриата и с 1 июля по 30 июля для магистратуры. Иностранные граждане должны подать документы непосредственно в приемную комиссию соответствующего высшего учебного заведения в установленном порядке. Иностранные граждане принимаются на обучение по программе бакалавриата по результатам тестирования, проводимого ежегодно 1 августа Государственным центром тестирования при Кабинете Министров. По результатам вступительных экзаменов, организуемых высшими учебными заведениями самостоятельно, они будут зачислены в магистратуру ориентировочно в период с 1 по 10 августа. На основании решения «Государственной комиссии по приему в высшие учебные заведения» Республики Узбекистан при Кабинете Министров прием иностранных граждан в ряды обучающихся осуществляется по результатам испытаний (для бакалавров) или вступительных экзаменов (для магистров).`
    },
    {
      title:`Как оплачивается обучение иностранных граждан?`,
      content:`Иностранные граждане принимаются к обучению только на основании договора об оплате труда в размере, установленном для иностранных граждан. Иностранные граждане оплачивают обучение в эквиваленте иностранной валюты по курсу, установленному Центральным банком на день оплаты, в национальной валюте. При этом иностранные граждане представляют банковское платежное поручение или квитанцию о том, что платеж произведен, а также справку об обмене иностранной валюты на национальную в банках Республики Узбекистан.
      За исключением иностранных граждан, имеющих вид на жительство в Республике Узбекистан. Они осуществляют платежи в эквиваленте иностранной валюты, исходя из курса, установленного Центральным банком на день платежа, в национальной валюте. Для них не требуется сертификат обмена иностранной валюты на национальную валюту.`
    },
    {
      title:`Сколько лет бакалавр или магистр в высшем учебном заведении, окончившие бакалавриат или магистратуру по государственным грантам, работают по направлению?`,
      content:`В соответствии с «Положением о порядке целевой подготовки кадров на основе государственных грантов», если выпускник обучается в бакалавриате и магистратуре высших учебных заведений на основе государственных грантов, его обязанность работать считается находящейся на не менее трех лет.`
    }
  ],
  question:`Часто задаваемые вопросы….`
  },

  en: {
    about: `The procedure for admission of students to the bachelor's degree of higher education institutions is carried out based on the decision No. 393.`,
    aboutData: [
      `According to it, higher education institutions carry out admissions in accordance with the admission quotas on the basis of state grants and payment contracts approved by the relevant decision of the President of the Republic of Uzbekistan or the Cabinet of Ministers`,
      ``,
      `Admission of students to full-time, part-time, special part-time and evening (shift) forms of education of higher educational institutions State Commission for admission to educational institutions of the Republic of Uzbekistan (hereinafter referred to as the State Commission ) is carried out according to the rating system of points collected according to the results of entrance exams (tests, professional (creative) exam, written exam).`,
      ``,
      `On the basis of the payment contract approved by the relevant decision of the President of the Republic of Uzbekistan or the Cabinet of Ministers, the admission of applicants within the admission quotas and outside the admission quotas is carried out by the State Commission in the manner specified in this Regulation.`,
      `Admission to higher education institutions is carried out on the basis of equal rights for all (both grants and fee-contracts), uniform admission rules and uniform selection. the right to be admitted in line is ensured. The rest of the applicants have the right to be accepted within the payment-contract quotas established on the basis of the rating of test scores.`,
      `The competition is held separately for each higher education institution according to the educational directions, languages ​​and forms of teaching. In the case of approval of the target admission quotas in the section of regions for certain directions, the competition will be held separately for each region.`,
      `The tests will be held in July-August in accordance with the decision of the President of the Republic of Uzbekistan No. PQ-4319 of May 14, 2019.
      Registration of 2022 applicants was carried out online through the my.dtm.uz platform or the my.gov.uz site. Registration was made from June 20 to July 20, 2022.`,
      `Applicants have the right to participate in the competition in up to five bachelor's degrees of higher education institutions.`,
      `In accordance with the Decree of the President of the Republic of Uzbekistan No. PQ-4319 of May 14, 2019 and Decree No. PF-113 of April 20, 2022, the following applicants are exempted from payment for the test:`,
      `- applicants who graduated from educational institutions in the year when the tests are organized;`,
      `- persons with disabilities;`,
      `- persons on full state support (graduates of "Mehribonlik" House and Children's Town);`,
      `- Persons and their children registered in the information system of the "Unified Register of Social Protection";`,
      `- Young people included in the "Youth register";`,
      `- applicants recommended directly for studentship on the basis of preference.`,
      ``,
      ` For the rest of the applicants, the test is considered paid (150,000 soums for 2022).`,
      `According to the report No. 13 of the State Commission, the set of subjects for the 2022-2023 academic year for the undergraduate education courses of the republic's higher education institutions is approved. You can get acquainted with this complex of subjects through the following link:`,
      {link:"https://t.me/dtmuzb/4673", text:"@dtmuzb/4673"},
      `In 3 subjects that are mandatory for all test-takers:`,
      `Mother tongue (Uzbek, Russian or Karakalpak language) - 10 questions.`,
      `1.1 points for each.`,
      `The maximum score is 11 points.`,
      ``,
      `Mathematics - 10 questions.`,
      `1.1 points for each.`,
      `The maximum score is 11 points.`,
      ``,
      `History of Uzbekistan - 10 questions.`,
      `1.1 points for each.`,
      `The maximum score is 11 points.`,
      ``,
      `The total possible score is 33 points.`,
      ``,
      `2 subjects suitable for the bachelor's degree:`,
      `Subject 1 - 30 questions.`,
      `3.1 points for each.`,
      `The maximum score is 93 points.`,
      `Subject 2 - 30 questions.`,
      `2.1 points for each.`,
      `The total possible score is 156 points.`,
      ``,
      `In general, applicants for the 2022-2023 academic year will be given a total of 90 test tasks in 5 subjects. The maximum possible score is 189 points.`,
      `The regulation approved by the decision of the Cabinet of Ministers No. 261 dated April 3, 2018 stipulates that 1 hour will be allocated for each 30 test tasks for completing the test tasks (including filling out the answer sheet).`,
      `Based on this, for the 2022-2023 academic year, 3 hours will be given for 90 test tasks in the entrance tests for the bachelor's education courses of higher educational institutions.`,
      `If the applicant has a certificate in one of the specialty subjects or passes a professional (creative) exam in one subject, 2 hours will be allocated to him for 1 specialty and 3 compulsory subjects.`,
      `If the applicant participates in the test only in compulsory subjects, he will be given 1 hour for 30 test tasks in 3 compulsory subjects.`,
      `This form of the test is the same for part-time, evening and foreign students.`,
    ],
    questions:[
      {
        title:"I am a citizen of Russia and I want to study at one of the Higher Education Institutions in Uzbekistan. How do I do this?",
        content:`Admission of foreign citizens to higher educational institutions in the Republic of Uzbekistan is carried out according to the results of the interview conducted by the higher educational institution. In this case, foreign citizens register as an applicant through the website of the higher education institution they are interested in.`
      },
      {
        title:`My nephew received a certificate issued by the Ministry of Family Support for women. Will my niece be admitted to a higher education institution with a state grant?`,
        content:`In 2021-2022 education year total of 2,000 separate state grant admission rates for women were allocated by region. Among the women who have a letter of recommendation according to these admission criteria, the one with the highest score will be accepted. Women who have a recommendation, but are not recommended for a separate state grant, can participate in the general competition for up to five undergraduate courses of their choice based on the results of the tests.`
      },
      {
        title:`I am a citizen of Russia and I want to study at one of the Higher Education Institutions in Uzbekistan. How do I do this?`,
        content:`Admission of foreign citizens to higher educational institutions in the Republic of Uzbekistan is carried out according to the results of the interview conducted by the higher educational institution. In this case, foreign citizens register as an applicant through the website of the higher education institution they are interested in.`
      },
      {
        title:`Graduates who have received training in higher education institutions on a fee-contract basis be placed in compulsory employment?`,
        content:`Graduates of higher education institutions who have received education at their own expense on a fee-contract basis can voluntarily participate in the distribution commission and, based on its approval, be sent to work at the jobs offered by the commission's decision. In this case, the distribution of graduates is carried out by the distribution commission of the higher education institution approved by the rector of the higher education institution.`
      },
      {
        title:`I received a military privilege. Does this privilege also transfer to correspondence education?`,
        content:`For the 2021/2022 academic year those who have a letter of recommendation from the command of the military unit in the section of higher education institutions and forms of education (full-time, part-time, evening). Based on the above, an applicant with a letter of recommendation from a military unit may be a candidate for the admission rate allocated for the part-time form of education.`
      },
      {
        title:`As a non-citizen, I cannot register online for undergraduate studies. Is there a different procedure for people like me?`,
        content:`Registration of stateless persons for admission to the bachelor's degree is carried out by admission commissions of higher education institutions.  Accordingly, you are asked to contact the admissions committee of the higher education institution you are interested in.`
      },
      {
        title:`I have been working in the bank for 11 years. My education is secondary-special. It was said that those with 5 years of work experience will be accepted on the basis of interviews without exams. Can I enroll through this scholarship?`,
        content:`The list of ministries, departments and large economic associations, which have the right to recommend studying in the part-time and evening forms of higher education of the Republic of Uzbekistan on the basis of a differentiated fee-contract according to the results of the interview, is approved annually by the State Commission. After the approval of the list of ministries and agencies with the right to issue a recommendation for the 2021/2022 academic year, based on the recommendation issued by the republican head office of the bank where you are operating, you will receive a higher education degree under a differentiated contract without exams and based on an interview you can enter the institution. For information: the Central Bank of the Republic of Uzbekistan and commercial banks were included in the list of ministries and agencies with the right to issue recommendations for the 2020/2021 academic year.`
      },
      {
        title:`Based on the needs of ministries, departments and large economic associations, is it allowed to pay the differentiated fee-contract amount of the students admitted to study based on their applications in the amount of the basic fee-contract?`,
        content:`PD-4359 of the President of the Republic of Uzbekistan dated June 17, 2019 "On the parameters of the state order for admission to higher education institutions of the Republic of Uzbekistan in the 2019/2020 academic year" ( <a href="https://lex.uz/docs/-4380357">lex.uz/docs/-4380357</a> ) , according to paragraph 6 of the decision, starting from the 2019/2020 academic year, based on the needs of ministries, agencies and large business associations (employers), full-time bachelor's degrees are offered to higher education institutions. The right to organize education in extramural and evening forms of education is granted on the basis of a differentiated fee-contract within the field of education. According to the relevant statement of the State Commission for Admission to Higher Education Institutions of the Republic of Uzbekistan, those who have at least 5 years of work experience in economic sectors and are admitted to higher education institutions of the Republic of Uzbekistan are eligible with an interview on the basis of a recommendation letter, the contract sums are determined in the amount of 2 times the annual contract sum starting from the 2019/2020 academic year.`
      },
      {
        title:`What is the procedure for admission of a foreign citizen to higher education institutions?`,
        content:`169 of the Cabinet of Ministers of August 4, 2008 "On improving the procedure for admission and training of foreign citizens to educational institutions of the Republic of Uzbekistan" Admission of foreign citizens to higher educational institutions of Uzbekistan (<a href="https://lex.uz/docs/-1380636">lex.uz/docs/-1380636</a>) is implemented in accordance with the decision. Documents of foreign citizens are accepted within the time limits set for citizens of the Republic of Uzbekistan, i.e. from June 20 to July 20 for the bachelor's degree, and from July 1 to July 30 for the master's degree. Foreign citizens must submit their documents directly to the admissions committee of the relevant higher education institution in accordance with the established procedure. Foreign citizens are admitted to the bachelor's degree on the basis of the results of tests conducted annually on August 1 by the State Testing Center under the Cabinet of Ministers. On the basis of the results of the entrance exams organized independently by higher education institutions, they will be admitted to the master's degree approximately between August 1 and 10. Based on the decision of the "State Commission for Admission to Higher Education Institutions" of the Republic of Uzbekistan under the Cabinet of Ministers, admission of foreign citizens to the ranks of students is carried out according to the results of tests (for bachelor's degrees) or entrance exams (for master's degrees).`
      },
      {
        title:` How are foreign citizens paid for their studies?`,
        content:`Foreign citizens are admitted to study only on the basis of a payment contract, and they pay the amount set for foreign citizens. Foreign citizens pay their tuition fees in the equivalent of a foreign currency, based on the exchange rate set by the Central Bank on the day of payment, in the national currency. At the same time, foreign citizens submit a bank payment order or receipt stating that the payment has been made, as well as a certificate of exchange of foreign currency into national currency in the banks of the Republic of Uzbekistan.
        Except for foreign citizens who have a residence permit in the Republic of Uzbekistan. They make payments in the equivalent of foreign currency, based on the exchange rate set by the Central Bank on the day of payment, in the national currency. They do not require a certificate of exchange of foreign currency into national currency.`
      },
      {
        title:`How many years does a bachelor's or master's degree in a higher education institution, who has completed a bachelor's or master's degree based on state grants, work on the basis of a referral? `,
        content:`According to the "Regulation on the procedure for targeted training of personnel on the basis of state grants", if a graduate studies at bachelor's and master's degrees of higher education institutions on the basis of state grants, his obligation to work is considered to be at least three years.`
      }
    ],
    question:`Frequently Asked Questions….`
  }
};
