    name36: "Духовно-просветительская работа: ",
    name37:
      "	В 2021/2022 учебном году в лицее проведен ряд положительных работ в области духовности и просвещения для обеспечения взаимной гармонии процессов обучения и воспитания. Согласно регулярно разрабатываемого плана все праздники занимающие важное место в жизни нашего народа и страны, исторические даты, молодежные мероприятия, встречи, беседы, круглые столы, проводятся совместно со сходом махаллинских граждан, органами внутренних дел, не государственными - не коммерческими организациями, районным хакимиятом, Республиканским духовно-просветительским центром, районным комитетом женщин, районным советом Союза молодежи и благотворительным общественным фондом «Махалля».",
    name38:
      "Информационно-ресурсный центр академического лицея располагает 18986 фондом книг, состоящими из учебников, политической, художественной, научной и популярной литературы. В ИРЦ (ARM) имеется возможность  использования электронных версий учебников и учебных пособий. Все компьютеры подключены к Интернету. Проведены акции «Новая книга», «Моя любимая книга», «В будущее с книгой», около 300 книг переданы в дар фонду ИРЦ (ARM) учащимися академического лицея, их родителями и преподавателями лицея.  ",
    name39: "Совместная работа:",
    name40:
      "	15 ноября 2021 года в академическом лицее Ташкентского химико-технологического института состоялась встреча с первым проректором МИРЭА - Российского технологического университета Солуновой И.С., и заместителем директора Международного Образовательного института Слезкиной Е.А. Основной целью этой встречи является установление отношений взаимного сотрудничества, налаживание взаимного обмена опытом между педагогическими кадрами и направление учащихся в избранную профессию, а также обмен квалифицированными педагогическими кадрами и привлечение их к занятиям, переговоры о создании «Мастер классов», с участием передовых специалистов химии и биологии. В ходе мероприятия была проведена презентация о МИРЭА – Российского технологического университета. Затем гости ответили на вопросы студентов. Мероприятие создало широкую возможность для выбора необходимой для себя профессии.",
