            p11: "2009-2013 yy. - Toshkent kimyo-texnologiya instituti sanoat ekologiyasi kafedrasi katta o’qituvchisi, 2013-2018 yy. - Toshkent kimyo-texnologiya instituti sanoat ekologiyasi kafedrasi mudiri, 2018-2020 yy. - Toshkent kimyo-texnologiya instituti yoshlar bilan ishlash bo’yicha prorektor. 2020 - h.v. - Toshkent kimyo-texnologiya instituti Ilmiy ishlar va innovatsiyalar bo'yicha prorektori.",
