          p15: "-- O‘zbekiston Respublikasi patenti № 04635. Sulfokationit olish usuli. Pulatov X.L., Turobjonov S.M., Berdieva M.I., Mutalov Sh.A., Tursunov T.T., Nazirova R.A., Abdutalipova N.M., Tashkent, 2013.",
