            p11: "В 2000-2001 годах. - Заместитель декана факультета химии органических веществ Ташкентского химико-технологического института, младший научный сотрудник научно-исследовательской лаборатории полимеров",
            p12: "В 2001-2005 годах. - Заведующий типографией Ташкентского химико-технологического института",
            p13: "2005-2007 гг.-доцент кафедры 'Промышленная экология' Ташкентского химико-технологического института",
            p14: "С 2007 по 2011 год. - Начальник отдела комитета по охране природы Ташкентской области",
            p15: "В 2011-2014 годах.  - Доцент кафедры 'Химическая технология переработки нефти и газа' Ташкентского химико-технологического института",
            p16: "2014-2017 годы. - Заведующий кафедрой 'машины и оборудование пищевой промышленности-основы механики' Ташкентского химико-технологического института",
            p17: "С 11 сентября 2017 года-проректор по учебной работе Ташкентского химико-технологического института.",
            p18: "Научные направления",
            p19: "- Создание технологии производства лака на основе отходов ацетиленового консерванта, нитро-и ацетацеллюлозы;",
            p20: "Педагогическая деятельность (с какого курса читает лекции)",
            p21: "Материаловедение против ГМТ",
            p22: "Основные обязанности в должности",
            p23: "- Организация реализации законов, указов и распоряжений Президента Республики Узбекистан, решений Олий Мажлиса и Кабинета министров в области образования и подготовки кадров;",
            p24: "- Организация учебной и учебно-методической работы и обеспечение подготовки квалифицированных кадров на основе государственных образовательных стандартов;",
            p25: "- Обеспечить полное знание всеми деканами и заведующими кафедрами, заведующими кафедрами и педагогическими работниками комплекса требований к содержанию знаний и уровню подготовки в соответствии с государственными образовательными стандартами;",
            p26: "- Организация исполнения приказов, распоряжений и указаний вышестоящих организаций, решений Ученого совета вуза по вопросам учебного процесса и приказов ректора;;",
            p27: "- Организация внедрения и эффективного использования в образовательном процессе прогрессивных форм обучения, в том числе дистанционного обучения, новых педагогических и информационно-коммуникационных технологий;",
            p28: "- Подготовка предложений и организация работы по совершенствованию учебной и учебно-методической работы;",
