                content:`Согласно действующему порядку, бакалавры и магистры высших учебных заведений, обучающиеся на основе государственных грантов по всем направлениям образования, в обязательном порядке распределяются по образовательным учреждениям при условии отработки не менее трех лет. В случае поступления выпускников высших учебных заведений, окончивших бакалавриат на основе государственных грантов, в магистратуру на платно-контрактной основе, обязанность отработать не менее трех лет наступает после окончания магистратуры.`
