                content:`В случае утверждения целевых квот приема по отдельным специальностям магистратуры в разрезе регионов, а также в разрезе заказчиков кадров в соответствии с Положением о порядке приема в магистратуру высших учебных заведений, отбор проводится отдельно по каждому региону, а также по заказчикам кадров (высшему образовательному учреждению). Абитуриенты, которые не были рекомендованы в студенческие ряды, не набрав достаточного количества баллов в конкурсе по целевому приему, участвуют в общем конкурсе с набранным баллом. После поступления студентов, принятых в магистратуру по целевому приему, между “вузом – заказчиком – и вузом-получателем образования” заключается соответствующий договор, предусматривающий отработку не менее 5 лет после окончания обучения в учреждении-заказчике. Для зачисления и участия в конкурсе на целевой прием в магистратуру рекомендательные письма не требуются.`
